.modal-title-newpost {
  color: #172261;
  font-size: 30px;
  margin-bottom: 2rem;
  margin-top: 0.5rem;
  text-align: center;
  font-weight: bold;
}
.modal-inputcont-newpost{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}.modal-input-newpost{
  border-radius: 0.6vw;
    width: 30rem;
    height: 2.5rem;
    border: #141E61 solid 1px;
    padding-left: 1rem;
    // font-style: italic;
    font-size: 1rem;
    outline: none;
}
.btn-apply-newpost {
  border-radius: 0.6rem;
  width: 10rem;
  height: 2.5rem;
  border: none;
  color: white;
  background-color: #1e8809;
  text-align: center;
  font-size: 18px;
  cursor: pointer;

  &:hover {
    color: white;
    background-color: #566573;
  }
}

.btn-cancel-newpost {
  border-radius: 0.6rem;
  width: 10rem;
  height: 2.5rem;
  border: none;
  color: white;
  background-color: #970234;
  text-align: center;
  font-size: 18px;
  cursor: pointer;

  &:hover {
    color: white;
    background-color: #566573;
  }
}
